import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import ProfileOverview from './Components/ProfileOverview';
class Profile extends Component {

    render() {
            return <div className='d-flex flex-column flex-column-fluid align-items-center justify-content-center w-100'>
                    <ProfileOverview />
                    <div className="container container-small">
                        <div className="mt-xxl-4 mt-2">
                            <h3 className="fs-6">Personal Info</h3>
                        </div>
                        <div className="card card-shadow-none">
                            <div className="card-body p-3">

                            <div className="row mb-3 mb-xl-4">
                                    <label className="col-sm-4 fw-semibold text-muted">User Roll</label>
                                    <div className="col-sm-8">
                                        <span className="fw-bold fs-7 text-gray-800">Manager</span>
                                    </div>
                                </div>

                                <div className="row mb-3 mb-xl-4">
                                    <label className="col-sm-4 fw-semibold text-muted">Full Name</label>
                                    <div className="col-sm-8">
                                        <span className="fw-bold fs-7 text-gray-800">Gopal Daiya</span>
                                    </div>
                                </div>
                                <div className="row mb-3 mb-xl-4">
                                    <label className="col-sm-4 fw-semibold text-muted">Company</label>
                                    <div className="col-sm-8 fv-row">
                                        <span className="fw-semibold text-gray-800 fs-7">Keenthemes</span>
                                    </div>
                                </div>
                                <div className="row mb-3 mb-xl-4">
                                    <label className="col-sm-4 fw-semibold text-muted">Contact Phone</label>
                                    <div className="col-sm-8 d-flex align-items-center">
                                        <span className="fw-bold fs-7 text-gray-800">044 3276 454 935</span>
                                    </div>
                                </div>
                                <div className="row mb-3 mb-xl-4">
                                    <label className="col-sm-4 fw-semibold text-muted">Email</label>
                                    <div className="col-sm-8">
                                        <span className="fw-bold fs-7 text-gray-800 me-2">gopal@gmail.com</span>
                                        <span className="badge text-light-alt bg-warning">Pending</span>
                                    </div>
                                </div>

                                <div className="row mb-3 mb-xl-4">
                                    <label className="col-sm-4 fw-semibold text-muted">Country</label>
                                    <div className="col-sm-8">
                                        <span className="fw-bold fs-7 text-gray-800">Germany</span>
                                    </div>
                                </div>
                                <div className="row mb-5">
                                    <label className="col-sm-4 fw-semibold text-muted">Allow Changes</label>
                                    <div className="col-sm-8">
                                        <span className="fw-semibold fs-7 text-gray-800">Yes</span>
                                    </div>
                                </div>
                                <div className="notice d-flex alert alert-warning border-dashed p-3">
                                    <span className="svg-icon svg-icon-2tx svg-icon-warning me-4">
                                        <svg className="w-30px h-30px" width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
                                            <rect opacity="0.3" x="2" y="2" width="20" height="20" rx="10" fill="currentColor"></rect>
                                            <rect x="11" y="14" width="7" height="2" rx="1" transform="rotate(-90 11 14)" fill="currentColor"></rect>
                                            <rect x="11" y="17" width="2" height="2" rx="1" transform="rotate(-90 11 17)" fill="currentColor"></rect>
                                        </svg>
                                    </span>
                                    <div className="d-flex flex-column flex-stack flex-grow-1">
                                        <h4 className="fs-6 fw-medium mb-1">Your email needs to be verified!</h4>
                                        <p className="mb-0">We have sent you and email with the verfication link to <strong>gopal@gmail.com</strong>. Kindly click on the verfication link present in the email to start exploring UGC Suit further.</p>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
              
    }
}

export default Profile;