import React, { Component } from 'react';
import Select from 'react-select';
import { countryPickerFun } from '../../../Services/Utils/utils.js';
import { Link } from 'react-router-dom';
import { OverlayTrigger, Tooltip } from 'react-bootstrap';
import ProfileOverview from './Components/ProfileOverview';
import Topabar from '../../Header/Components/Topbar.js';

class EditProfile extends Component {
    
    constructor(props) {

        console.log(props,'propsprops')
        super(props);
        this.state = {
            resetPasswordToggle:false
        };
    }

    resetPasswordToggle = () =>{
        this.setState({
            resetPasswordToggle: !this.state.resetPasswordToggle
        })
    }

   
    componentDidMount(){

        countryPickerFun().then((response) => {
            console.log(response,'response')
        })   
    }

    render() {

        const {resetPasswordToggle} = this.state;

            return <div className="d-flex flex-column flex-column-fluid align-items-center justify-content-center w-100">
               
                  <ProfileOverview />
                    <div className="container container-small">
                        <div className="mt-xxl-4 mt-2">
                            <h3 className="fs-6">Edit Info</h3>
                        </div>

                        <div className="card card-shadow-none mb-xxl-4 mb-3">
                            <div className="card-body p-3 needs-validation">

                         <div className='row'>
                            <div className="col-lg-6 col-sm-12  input-block">
                                <label className="fw-semibold text-muted mb-2">Manager Roll
                                <strong className="text-danger">*</strong></label> {/** is-valid */}
                                    <input type="admin_roll" name="tag_title" className="form-control form-control-lg mb-3 mb-lg-0" value="Max"/>
                                </div>


                            <div className="col-lg-6 col-sm-12 input-block">
                                <label className="fw-semibold text-muted mb-2"> Name
                                <strong className="text-danger">*</strong></label> {/** is-valid */}
                                    <input type="number" name="admin_name" className="form-control form-control-lg mb-3 mb-lg-0" value="Max"/>
                                </div>


                            <div className="col-lg-6 col-sm-12 input-block">
                                <label className="fw-semibold text-muted mb-2"> Mobile No
                                <strong className="text-danger">*</strong></label> {/** is-valid */}
                                    <input type="number" name="admin_mobile_no" className="form-control form-control-lg mb-3 mb-lg-0" value="Max"/>
                                </div>


                            <div className="col-lg-6 col-sm-12 input-block">
                                <label className="fw-semibold text-muted mb-2"> Alternate Mobile No
                                <strong className="text-danger">*</strong></label> {/** is-valid */}
                                    <input type="number" name="admin_alternate_mobile_no" className="form-control form-control-lg mb-3 mb-lg-0" value="Max"/>
                                </div>


                            <div className="col-lg-6 col-sm-12 input-block">
                                <label className="fw-semibold text-muted mb-2"> Email Id
                                <strong className="text-danger">*</strong></label> {/** is-valid */}
                                    <input type="number" name="admin_mail_id" className="form-control form-control-lg mb-3 mb-lg-0" value="Max"/>
                                </div>
                


                            <div className="col-lg-6 col-sm-12 input-block">
                                <label className="fw-semibold text-muted mb-2"> Address
                                <strong className="text-danger">*</strong></label> {/** is-valid */}
                                    <input type="number" name="admin_address" className="form-control form-control-lg mb-3 mb-lg-0" value="Max"/>
                                </div>

                            </div> 
                            
                                <div className="row mb-3 mb-xl-4">
                                    <label className="col-sm-4 fw-semibold text-muted mb-2">Profile</label>
                                    <div className="col-sm-12">
                                        <div className="d-flex align-items-center">
                                            <div className="me-3 me-xxl-4">
                                                <div className="symbol h-90px w-90px position-relative rounded-circle overflow-hidden">
                                                    <img src="https://i.pinimg.com/736x/bc/4b/bd/bc4bbd2ce6b43c390187869173b36130.jpg" alt="profile img" className="img-fluid object-center-fit"/>
                                                </div>
                                            </div>
                                            <div className="d-flex align-items-center flex-wrap">
                                                <div className="d-flex flex-column align-items-center flex-nowrap mb-0">
                                                    <OverlayTrigger placement="top" overlay={<Tooltip>Edit</Tooltip>}>
                                                        <button type="button" className="btn btn-icon btn-active-light p-0 w-30px h-30px transition-none border-0 my-1">
                                                            <span className="svg-icon svg-icon-5">
                                                                <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" width="24" height="24">
                                                                    <path fill="none" d="M0 0h24v24H0z"/>
                                                                    <path d="M12.9 6.858l4.242 4.243L7.242 21H3v-4.243l9.9-9.9zm1.414-1.414l2.121-2.122a1 1 0 0 1 1.414 0l2.829 2.829a1 1 0 0 1 0 1.414l-2.122 2.121-4.242-4.242z" fill="currentColor"/>
                                                                </svg>
                                                            </span>
                                                        </button>
                                                    </OverlayTrigger>
                                                    <OverlayTrigger placement="top" overlay={<Tooltip>Remove</Tooltip>}>
                                                        <button type="button" className="btn btn-icon btn-active-light p-0 w-30px h-30px transition-none border-0 my-1">
                                                            <span className="svg-icon svg-icon-5">
                                                                <svg xmlns="http://www.w3.org/2000/svg" className="symbol-20" viewBox="0 0 24 24" width="24" height="24">
                                                                    <path fill="none" d="M0 0H24V24H0z"></path>
                                                                    <g>
                                                                    <path d="M17 6h5v2h-2v13a1 1 0 0 1-1 1H5a1 1 0 0 1-1-1V8H2V6h5V3a1 1 0 0 1 1-1h8a1 1 0 0 1 1 1v3zm1 2H6v12h12V8zm-9 3h2v6H9v-6zm4 0h2v6h-2v-6zM9 4v2h6V4H9z" fill="currentColor"></path>
                                                                    </g>
                                                                </svg>
                                                            </span>
                                                        </button>
                                                    </OverlayTrigger>
                                                </div>
                                            </div>
                                        </div>
                                        <div className="form-text mt-2">Allowed file types: png, jpg, jpeg.</div>
                                    </div>
                                                        
                                    <div className="d-flex align-items-center justify-content-end flex-wrap mt-3 top-18">
                                        <button type="button" className="btn btn-sm btn-secondary me-3">Cancel</button>
                                        <button type="button" className="btn btn-sm btn-primary">Save</button>
                                    </div>
                                </div>
                            </div>
                        </div>

                        <div className="mt-xxl-4 mt-2">
                            <h3 className="fs-6">Change Password</h3>
                        </div>
                        <div className="card card-shadow-none mb-xxl-4 mb-3">
                            <div className="card-body p-3 needs-validation">
                                {!resetPasswordToggle ? 
                                <div className="mb-2 mb-xl-3 d-flex align-items-center justify-content-between flex-wrap">
                                    <div className="d-flex flex-column flew-grow-1">
                                        <label className="fw-semibold text-muted mb-2">Password</label>
                                        <span className="fw-bold fs-7 text-gray-800">**************</span>
                                    </div>
                                    <div className="d-flex mt-4">
                                        <button type="button" className="btn btn-sm btn-primary me-2" onClick={this.resetPasswordToggle}>Reset Password</button>
                                    </div>
                                </div>
                                :
                                <div className="row mb-2 mb-xl-3">
                                    <div className="col-lg-4 mb-3 mb-lg-0">
                                        <label className="fw-semibold text-muted mb-2">Current Password</label>
                                        <input type="password" name="fname" className="form-control form-control-lg is-invalid" placeholder="Current Password" defaultValue="Max"/>
                                        <div className="invalid-feedback">Please enter correct password.</div>
                                    </div>
                                    <div className="col-lg-4 col-sm-6 mb-3 mb-sm-0">
                                        <label className="fw-semibold text-muted mb-2">New Password</label>
                                        <input type="password" name="lname" className="form-control form-control-lg is-invalid" placeholder="Last name" defaultValue="Smith"/>
                                        <div className="invalid-feedback">Password must be at least 8 character and contain symbols</div>
                                    </div>
                                    <div className="col-lg-4 col-sm-6">
                                        <label className="fw-semibold text-muted mb-2">Confirm New Password</label>
                                        <input type="password" name="lname" className="form-control form-control-lg is-valid" placeholder="Last name" defaultValue="Smith"/>
                                        <div className="valid-feedback">Password match.</div>
                                    </div>
                                    <div className="col-sm-12 mt-2">
                                        <div className="text-muted">Password must be at least 8 character and contain symbols</div>
                                        <div className="d-flex align-items-center justify-content-end flex-wrap mt-3 mt-lg-0">
                                            <button type="button" className="btn btn-sm btn-secondary me-3" onClick={this.resetPasswordToggle}>Cancel</button>
                                            <button type="button" className="btn btn-sm btn-primary">Update Password</button>
                                        </div>
                                    </div>
                                </div>
                                }
                            </div>
                        </div>

                        <div className="mt-xxl-4 mt-2">
                            <h3 className="fs-6">Deactivate Account</h3>
                        </div>
                        <div className="card card-shadow-none mb-xxl-4 mb-3">
                            <div className="card-body p-3 needs-validation">
                                <div className="notice d-flex alert alert-warning border-dashed p-3">
                                    <span className="svg-icon svg-icon-2tx svg-icon-warning me-4">
                                        <svg className="w-30px h-30px" width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
                                            <rect opacity="0.3" x="2" y="2" width="20" height="20" rx="10" fill="currentColor"></rect>
                                            <rect x="11" y="14" width="7" height="2" rx="1" transform="rotate(-90 11 14)" fill="currentColor"></rect>
                                            <rect x="11" y="17" width="2" height="2" rx="1" transform="rotate(-90 11 17)" fill="currentColor"></rect>
                                        </svg>
                                    </span>
                                    <div className="d-flex flex-column flex-stack flex-grow-1">
                                        <h4 className="fs-6 fw-medium mb-1">You Are Deactivating Your Account</h4>
                                        <p className="mb-0">For extra security, this requires you to confirm your email or phone number when you reset yousignr password. <Link className="fw-bold text-decoration-none" to="/">Learn more</Link></p>
                                    </div>
                                </div>
                                <div className="row">
                                    <div className="col-12 mb-3">
                                        <div className="form-check d-flex">
                                            <input name="deactivate" className="form-check-input is-invalid" type="checkbox" defaultValue="" id="deactivate"/>
                                            <label className="form-check-label fs-7 fw-semibold" htmlFor="deactivate">I confirm my account deactivation</label>
                                        </div>
                                    </div>
                                </div>
                                <div className="row mb-3 mb-xl-4">
                                    <div className="col-lg-4 col-sm-6">
                                        <label className="fw-semibold text-muted mb-2">Enter Password <strong className="text-danger">*</strong></label>
                                        <input type="password" name="fname" className="form-control form-control-lg mb-3 mb-lg-0 is-valid" placeholder="Current Password" defaultValue="Max"/>
                                    </div>
                                    <div className="col-sm-12">
                                        <div className="d-flex align-items-center justify-content-end flex-wrap mt-3 mt-lg-0">
                                            <button type="button" className="btn btn-sm btn-danger">Deactivate Account</button>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
              
    }
}

export default EditProfile;