import React from 'react';
import { Route, Routes } from 'react-router-dom';
import Header from '../Header/Header'
import Aside from '../Aside/Aside'
import Home from './Home/Home';
import MyAssets from './MyAssets/MyAssets';
import MyAssetsItems from './MyAssets/Assets/AssetsItem';
import Connections from './Connections/Connections';
import Recents from './Recents/Recents';
import Billing from './Billing/Billing';
import Profile from './Profile/Profile';
import EditProfile from './Profile/EditProfile';
import Notifications from './Notifications/Notifications';
import Error from './error';
import Login from './Login';
import ForgotPassword from './Forgotpassword';

import UpdateCommission from './master/update-commission'; 
import ManagerList from './Manager/List';
import ManagerAdd from './Manager/Add';
import ManagerEdit from './Manager/Edit';

import UserChat from './Users/Chat';
import Brands from './Users/Brands';
import Influencer from './Users/Influencer';

import AddCategory from './creatorTags/Category/add';
import EditCategory from './creatorTags/Category/edit';
import ListCategory from './creatorTags/Category/list';

import AddSubCategory from './creatorTags/subCategory/add';
import EditSubCategory from './creatorTags/subCategory/edit';
import ListSubCategory from './creatorTags/subCategory/list';
import EmailVerify from './master/Email-verify';

import { ToastContainer } from 'react-toastify';
import { Detector } from "react-detect-offline";
import { BASE_URL } from "../../actions/api";
import { useNavigate } from 'react-router-dom';
import { useState } from 'react';
 
const Wrapper = ({ children }) => { 

    let navigate = useNavigate();

    //  if(localStorage.getItem('user')==null){
    //        navigate('/login');  
    //    }

    var pageName = window.location.pathname;
    
    pageName = pageName.split("/");
    //console.log('pageName', pageName)
    const SubHeaderPages = ["home", ""];
    const ActiveSubheader =  SubHeaderPages.includes(pageName[2]);
    
    const AfterLogin = ["login", "forgot-password"];
    const AfterLoginPage =  AfterLogin.includes(pageName[2]);

    return <><div className={ !AfterLoginPage ? `app-page d-flex flex-column flex-column-fluid flex-column align-items-stretch 
    h-100 ${!ActiveSubheader ? ' has-subheader' : ''}` : null}>
              
              { !AfterLoginPage ? 
              
                   <>
                     <Header />
                     <Aside />
                  </> : null}
                
                <main className="main_content">
                    {children}
                </main>


         

         <ToastContainer 
            position="bottom-right"
            autoClose={1000}
            hideProgressBar={false}
            newestOnTop={false}
            closeOnClick
            rtl={false}
            pauseOnFocusLoss
            draggable
            pauseOnHover
            theme="colored" />
        </div>

        <Detector style={{display:"none"}}
        render={({ online }) => ( 
            <div className={online ? "net-online" : "net-offline"}>
            {online ? "" : "Network Problem"}
        </div>
        )} />

    </>
}


function MainCompomentRouter() { 
    const [token, setToken] = useState(localStorage.getItem("token"))

    console.log(token,'tokentoken')

    if(!token) {
        console.log('if---------')
       /// return <Login />

       return <Routes>
        <Route exact path="/" element={<Login />}></Route>
        <Route exact path="/forgot-password" element={<Wrapper><ForgotPassword /></Wrapper>}></Route>
       </Routes>
    }
    else {
        console.log('else---------')
        return <Routes>
            <Route exact path="/forgot-password" element={<Wrapper><ForgotPassword /></Wrapper>}></Route>
            <Route exact path="/" element={<Wrapper><Home /></Wrapper>}></Route>
            <Route exact path="/home" element={<Wrapper><Home /></Wrapper>}></Route>
            <Route path="/assets" element={<Wrapper><MyAssets /></Wrapper>}></Route>
            <Route path="/assets/:items" element={<Wrapper><MyAssetsItems /></Wrapper>}></Route>
            <Route path="/connections" element={<Wrapper><Connections /></Wrapper>}></Route>
            <Route path="/recents" element={<Wrapper><Recents /></Wrapper>}></Route>
            <Route path="/billing" element={<Wrapper><Billing /></Wrapper>}></Route>
            <Route path="/notification" element={<Wrapper><Notifications /></Wrapper>}></Route>
            <Route path="/profile" element={<Wrapper><Profile /></Wrapper>}></Route>
            <Route path="/profile/edit" element={<Wrapper><EditProfile /></Wrapper>}></Route>
            
            <Route path='/commission-setting' element={<Wrapper><UpdateCommission title="Update Commission"/></Wrapper>} />
            <Route path='/email-verify' element={<Wrapper><EmailVerify title="Email Verify"/></Wrapper>} />

            <Route path='/manager-list'  element={<Wrapper><ManagerList link="add" pagelink="/manager-add" title="Mangers" filter="1" refresh="1"/></Wrapper>}/>
            <Route path='/manager-add'  element={<Wrapper><ManagerAdd  title="Add Mangers" /></Wrapper>}/>
            <Route path='/manager-edit'  element={<Wrapper><ManagerEdit title="Edit Mangers"/></Wrapper>}/>

            <Route path='/brands'  element={<Wrapper><Brands  title="Brands" filter="1" refresh="1"/></Wrapper>}/>
            <Route path='/influencer'  element={<Wrapper><Influencer title="Influencer" filter="1" refresh="1"/></Wrapper>}/>
            <Route path='/user-chat'  element={<Wrapper><UserChat  filter="1" refresh="1"/></Wrapper>}/>

            <Route path='/chat'  element={<Wrapper><UserChat title="communication"/></Wrapper>}/>

            <Route path='/add-category'  element={<Wrapper><AddCategory  title="Add Tags Category"/></Wrapper>}/>
            <Route path='/edit-category/:id'  element={<Wrapper><EditCategory title="Edit Tags Category"/></Wrapper>}/>
            <Route path='/list-category'  element={<Wrapper><ListCategory link="add" title="Tags Category" pagelink="/add-category" filter="1" refresh="1"/></Wrapper>}/>


            <Route path='/add-sub-category'  element={<Wrapper><AddSubCategory  title="Add Tags Sub Category"/></Wrapper>}/>
            <Route path='/edit-sub-category/:id' element={<Wrapper><EditSubCategory  title="Edit Tags Sub Category"/></Wrapper>}/>
            <Route path='/list-sub-category' element={<Wrapper><ListSubCategory link="add" title="Tags Sub Category"  pagelink="/add-sub-category" filter="1" refresh="1"/></Wrapper>}/>

        </Routes>}
}

export default MainCompomentRouter;