import React from "react";
import { useState, useEffect } from "react";
import { Button, OverlayTrigger, Table, Tooltip } from "react-bootstrap";
import Topabar from "../../Header/Components/Topbar";
import Modal from "react-bootstrap/Modal";
import { useForm } from "react-hook-form";
import { toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import {  ADD_MAIL_VERIFY, GET_MAIL_VEIFY, UPDATE_MAIL_STATUS } from "../../../actions/authenticationAction";
import Spinner from 'react-bootstrap/Spinner';
import { confirmAlert } from "react-confirm-alert";


function Emailverify(props) {

  const [ commissionForm,setcommissionForm]=useState(false);  
  const [isDisabled, setDisabled] = useState(false);

  const [mail, setMail] = useState([])

  const { register, handleSubmit, formState: { errors },reset,setValue } = useForm();
  
  const changeStatus=(id,type)=>{

    UPDATE_MAIL_STATUS(id,type).then(res => {
      const { data } = res
        if(data.status){ 

              if(type=='del'){
                setMail(mail.filter((data)=>{ 
                      return data.mail_verify_id!=id 
                  }))
              }
             toast.success(data.msg)
          }
        else toast.error(data.msg)
      }).catch(error=>{
          toast.error(error.message)
      })
  }


  
const deleteData = (id,type)=>{
   confirmAlert({
    message: 'Are you sure delete this record !',
    buttons: [
      {
        label: 'Yes',
        onClick: () => changeStatus(id,type)
      },
      {
        label: 'No',
        onClick: () => {}
      }
    ]
 });
}  

  const onSubmit = async (values) => {
    setDisabled(true);
    const { mail_verify } = values
    ADD_MAIL_VERIFY(mail_verify).then(res => {
    const { data } = res
      if(data.status){ 
           toast.success(data.msg)
           getMail()
           setcommissionForm()
           reset();
        }
      else toast.error(data.msg)
      setDisabled(false);
    }).catch(error=>{
        setDisabled(false);
        toast.error(error.message)
    })
  }


   const getMail=()=>{

    GET_MAIL_VEIFY().then(res => {
        const { data } = res
        if(data.status){
          setMail(data.data)
        }
      })
   }

  useEffect(() => {
    getMail()
  }, [])


  return (
    <>
      <div className="d-flex flex-column flex-column-fluid align-items-center justify-content-center w-100 bg-gray">
        <Topabar data={props} />
        
        <div className="container">
          <div className="row justify-content-md-center">
            <div className="col-lg-10 col-12 pt-lg-5 pt-3">
              <div className="plan-details mb-md-5 mb-4">
                <div className="card">
                  <div className="card-body px-lg-4 py-lg-5 p-3">
                    <div className="row align-items-md-center">

                      <div className="col-lg-12 col-sm-6 col-12">
                        <div className="text-md-center">
                          <button style={{float:'right'}}
                            onClick={(e) => setcommissionForm(true)}
                            className="btn btn-primary d-flex align-items-center"
                          >
                            <small>Add Mail</small>
                          </button>
                        </div>
                      </div>
                    </div>


                 { mail && mail.length ?  

                  <Table responsive className="mb-0">
                    <thead>
                        <tr>
                        <th className="p-lg-6 p-sm-3 p-2">#</th>
                        <th className="p-lg-6 p-sm-3 p-2">Mail</th>
                        <th className="p-lg-6 p-sm-3 p-2"></th>
                        </tr>
                    </thead>
                    <tbody>

                    { mail.map((dt,index) => (
                        <tr className="align-middle">
                        <td className="p-lg-6 p-sm-3 p-2 text-nowrap">{index+1}</td>
                        <td className="p-lg-6 p-sm-3 p-2 text-nowrap">{dt.mail_verify}</td>

                        <td className="p-lg-4 p-sm-3 p-2 text-nowrap text-end">
                            <OverlayTrigger placement="top" overlay={<Tooltip>Active</Tooltip>}>
                                <button className="btn btn-icon btn-active-light">
                                <label className='switch position-relative'>
                                    <input onChange={(e)=>changeStatus(dt.mail_verify_id,'type')} defaultChecked={dt.mail_status ? true : false} type="checkbox" role="switch" id="flexSwitchCheckDefault"/>
                                    <span></span>
                                </label>
                            </button>
                            </OverlayTrigger> &nbsp;&nbsp;
 


                        <OverlayTrigger placement="top" overlay={<Tooltip>Delete</Tooltip>}>
                            <button onClick={(e)=>deleteData(dt.mail_verify_id,'del')}   className='btn btn-icon btn-active-light'>
                                <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24">
                                    <g data-name="Group 4331" transform="translate(10956 -4533)">
                                        <rect id="Rectangle_2069" data-name="Rectangle 2069" width="24" height="24" transform="translate(-10956 4533)" fill="none" opacity="0"></rect>
                                        <g id="Group_4324" data-name="Group 4324" transform="translate(-10952 4536)">
                                        <path id="Path_5188" data-name="Path 5188" d="M2.722,16.328A2,2,0,0,0,4.694,18h6.612a2,2,0,0,0,1.972-1.672L14,6H2ZM9,9.5A.5.5,0,0,1,9.5,9h1a.5.5,0,0,1,.5.5v5a.5.5,0,0,1-.5.5h-1a.5.5,0,0,1-.5-.5Zm-4,0A.5.5,0,0,1,5.5,9h1a.5.5,0,0,1,.5.5v5a.5.5,0,0,1-.5.5h-1a.5.5,0,0,1-.5-.5ZM15,2H11A2,2,0,0,0,9,0H7A2,2,0,0,0,5,2H1A1,1,0,0,0,1,4H15a1,1,0,0,0,0-2Z" fill="#545454" fill-rule="evenodd"></path>
                                        </g>
                                    </g>
                                    </svg>
                                </button></OverlayTrigger>
                                
                            </td>
                        </tr>
                    ))}     

                    </tbody>
                    </Table> : <div className="data-not-found">Data not exist</div> }

                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>

        <Modal show={commissionForm} onHide={(e) => setcommissionForm(false)}>
          <Modal.Header closeButton>
            <Modal.Title>Add Email</Modal.Title>
          </Modal.Header>

          <form
            onSubmit={handleSubmit(onSubmit)}
            noValidate
            method="post"
            action=""
            autoComplete="off"
            className="m-form m-form--fit m-form--label-align-right"
            encType="multipart/form-data"
          >
            <Modal.Body>
              <div className="row mb-12 mb-xl-12">
                <div className="col-lg-12 col-sm-12">
                  <label className="fw-semibold text-muted mb-2">
                    mail <strong className="text-danger">*</strong>
                  </label>
                  <input
                    {...register("mail_verify", {
                      required: true,
                      required: "required field.",
                    })}
                    type="text"
                    className={`form-control form-control-lg mb-3 mb-lg-0 ${errors.mail_verify ? `is-invalid` : null} `}
                    defaultValue=""
                  />
                  {errors.mail_verify && (
                    <p className="text-error">{errors.mail_verify.message}</p>
                  )}
                </div>
              </div>
            </Modal.Body> { console.log(isDisabled,'isDisabledisDisabled')}
            <Modal.Footer>
              <Button ty
                variant="secondary"
                onClick={(e) => setcommissionForm(false)}
              >
                Close
              </Button>
              <Button
                type="submit"
                disabled={isDisabled ? 'disabled' : null}
                variant="primary">
                Save  { isDisabled ? <Spinner animation="border" role="status"> </Spinner> : null }
              </Button>

             
            </Modal.Footer>
          </form>
        </Modal>
      </div>
    </>
  );
}

export default Emailverify;
