import React from 'react';
import { useState,useEffect } from 'react';
import { OverlayTrigger, Table, Tooltip } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import { GET_MANPOWER } from '../../../actions/authenticationAction';
import Topabar from '../../Header/Components/Topbar';
import Pagging from '../include/Pagging';
import moment from 'moment'

 function Managers(props) {

    const [isFilter,setisFilter] = useState(false);
    const [getData,setgetData] = useState([]);

    useEffect(() => { 

      GET_MANPOWER().then(res => {
          const { data } = res
          if(data.status){
             setgetData(data.data)
          }
      })
   
}, []) 

 return (
      <>

<div className='d-flex flex-column flex-column-fluid align-items-center justify-content-center w-100 bg-gray'>
    <Topabar isFilter={isFilter} setisFilter={setisFilter} data={props}/>
    
        <div className="container container-small top-18">

        {isFilter ? 
         <div className="card card-shadow-none mb-xxl-4 mb-3">
            <div className="card-body p-3 needs-validation">
            <div className='row'>
              <div className="col-lg-4 col-sm-12  input-block">
                <label className="fw-semibold text-muted mb-2">Manager Roll
                  </label> {/** is-valid */}
                    <select id="admin_roll" name="admin_roll" className="form-control form-control-lg mb-3 mb-lg-0">
                       <option>Business development manager  </option>
                       <option>Accounting manager </option>
                       <option>Dispute resolution manager</option>
                    </select>
                  </div>

              <div className="col-lg-3 col-sm-12 input-block">
                <label className="fw-semibold text-muted mb-2"> Name
                  </label> {/** is-valid */}
                    <input type="number" name="admin_name" className="form-control form-control-lg mb-3 mb-lg-0" value="Max"/>
                  </div>
               


               <div className="col-lg-2 col-sm-12 input-block">
                <label className="fw-semibold text-muted mb-2"> From
                  </label> {/** is-valid */}
                    <input type="date" name="admin_name" className="form-control form-control-lg mb-3 mb-lg-0" defaultValue={''} />
                  </div>
              


               <div className="col-lg-2 col-sm-12 input-block">
                <label className="fw-semibold text-muted mb-2"> To
                  </label> {/** is-valid */}
                    <input type="date" name="admin_name" className="form-control form-control-lg mb-3 mb-lg-0" defaultValue={''}/>
                  </div>
               </div> 

                <div className="d-flex align-items-center justify-content-end flex-wrap mt-3 top-18">
                    <button type="button" className="btn btn-sm btn-primary">Filter</button>
                </div>      
            </div>
          </div> : null }
          

                        <div className="card card-shadow-none bottom-15">
                            <div className="card-body p-lg-4 p-3">
                                <Table responsive className="mb-0">
                                    <thead>
                                        <tr>
                                            <th className="p-lg-6 p-sm-3 p-2">#</th>
                                            <th className="p-lg-6 p-sm-3 p-2">Name</th>
                                            <th className="p-lg-6 p-sm-3 p-2">Roll</th>
                                            <th className="p-lg-6 p-sm-3 p-2">Date</th>
                                            <th className="text-end p-lg-6 p-sm-3 p-2" align="right">Action</th>
                                        </tr>
                                    </thead>
                                    <tbody>

                                      { getData.length>0  ?
                                      
                                         getData.map((dt,index)=>(

                                          <tr className="align-middle">
                                            <td className="p-lg-6 p-sm-3 p-2 text-nowrap">{index+1}</td>
                                            <td className="p-lg-6 p-sm-3 p-2 text-nowrap">{dt.admin_name}</td>
                                            <td className="p-lg-6 p-sm-3 p-2 text-nowrap">{dt.admin_roll=='BM' ? 'Business development' : (dt.admin_roll=='AC' ? 'Accounting' : 'Resolution')}</td>
                                            <td className="p-lg-6 p-sm-3 p-2 text-nowrap">{moment(dt.created_at).format('DD-MM-YYYY HH:mm')}</td>
                                            <td className="p-lg-6 p-sm-3 p-2 text-nowrap text-end">
                                                    

                                            <OverlayTrigger placement="top" overlay={<Tooltip>Active</Tooltip>}>
                                                 <button className="btn btn-icon btn-active-light">
                                                    <label className='switch position-relative'>
                                                        <input type="checkbox" role="switch" id="flexSwitchCheckDefault"/>
                                                        <span></span>
                                                    </label>
                                                </button>
                                              </OverlayTrigger>  


                                            <OverlayTrigger placement="top" overlay={<Tooltip>Profile</Tooltip>}>
                                              <Link to="/profile">
                                                <button className="btn btn-icon btn-active-light">
                                                 <svg width="24" height="24" fill="#212529" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 32 32"><path d="M16 31C7.729 31 1 24.271 1 16S7.729 1 16 1s15 6.729 15 15-6.729 15-15 15zm0-28C8.832 3 3 8.832 3 16s5.832 13 13 13 13-5.832 13-13S23.168 3 16 3z"/><circle cx="16" cy="15.133" r="4.267"/><path d="M16 30c2.401 0 4.66-.606 6.635-1.671-.425-3.229-3.18-5.82-6.635-5.82s-6.21 2.591-6.635 5.82A13.935 13.935 0 0 0 16 30z"/></svg>
                                                </button></Link>
                                            </OverlayTrigger>


                                            <OverlayTrigger placement="top" overlay={<Tooltip>Edit</Tooltip>}>
                                              <Link to="/manager-edit">
                                                <button className="btn btn-icon btn-active-light">
                                                    <svg width="24" height="24" fill="none" xmlns="http://www.w3.org/2000/svg"><g clip-path="url(#a)" fill-rule="evenodd" 
                                                        clip-rule="evenodd" fill="#212529"><path d="M21.707 7.293a1 1 0 0 1 0 1.414l-13 13A1 1 0 0 1 8 22H4a1 1 0 0 1-1-1v-4a1 1 0 0 1 .293-.707l13-13a1 1 0 1 1 1.414 1.414L5 17.414V20h2.586L20.293 7.293a1 1 0 0 1 1.414 0z"/><path d="M16.293 3.293a1 1 0 0 1 1.414 0l4 4a1 1 0 0 1-1.414 1.414l-4-4a1 1 0 0 1 0-1.414zM17.293 11.707l-4-4 1.414-1.414 4 4-1.414 1.414z"/></g><defs><clipPath id="a"><path fill="#fff" d="M0 0h24v24H0z"/></clipPath></defs></svg>
                                                </button></Link>  
                                             </OverlayTrigger>

                                            <OverlayTrigger placement="top" overlay={<Tooltip>Delete</Tooltip>}>
                                                <button className='btn btn-icon btn-active-light'>
                                                    <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24">
                                                        <g data-name="Group 4331" transform="translate(10956 -4533)">
                                                            <rect id="Rectangle_2069" data-name="Rectangle 2069" width="24" height="24" transform="translate(-10956 4533)" fill="none" opacity="0"></rect>
                                                            <g id="Group_4324" data-name="Group 4324" transform="translate(-10952 4536)">
                                                            <path id="Path_5188" data-name="Path 5188" d="M2.722,16.328A2,2,0,0,0,4.694,18h6.612a2,2,0,0,0,1.972-1.672L14,6H2ZM9,9.5A.5.5,0,0,1,9.5,9h1a.5.5,0,0,1,.5.5v5a.5.5,0,0,1-.5.5h-1a.5.5,0,0,1-.5-.5Zm-4,0A.5.5,0,0,1,5.5,9h1a.5.5,0,0,1,.5.5v5a.5.5,0,0,1-.5.5h-1a.5.5,0,0,1-.5-.5ZM15,2H11A2,2,0,0,0,9,0H7A2,2,0,0,0,5,2H1A1,1,0,0,0,1,4H15a1,1,0,0,0,0-2Z" fill="#545454" fill-rule="evenodd"></path>
                                                            </g>
                                                        </g>
                                                        </svg>
                                                    </button></OverlayTrigger>
                                                </td>
                                        </tr>

                                        ))
                                        : 
                                      <tr>
                                        <td colSpan={5}>
                                        <div className="data-not-found">Data not exist</div>
                                      </td></tr> }
                                    
                                    </tbody>
                                </Table>
                            </div>
                        </div>

                   {getData.length>0 ?     
                    <Pagging/> : null }

                    </div>
              </div>                                                               
     </>
   )
}

export default Managers
