import React from 'react';
import { useState,useEffect } from 'react';
import Topabar from '../../../Header/Components/Topbar';
import { useForm } from "react-hook-form";
import { toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import { ADD_CATEGORY } from '../../../../actions/authenticationAction';
import { Spinner } from 'react-bootstrap';

  function AddCategory(props) {

  const [isDisabled, setDisabled] = useState(false);
  const { register, handleSubmit, formState: { errors },reset,setValue } = useForm();

  const onSubmit = async (values) => {
    setDisabled(true);
    ADD_CATEGORY(values).then(res => {
    const { data } = res
      if(data.status){ 
           toast.success(data.msg)
           reset()
        }
      else toast.error(data.msg)
      setDisabled(false);
    }).catch(error=>{
        setDisabled(false);
        toast.error(error.message)
    })
  }


    return (
      <>

<div className='d-flex flex-column flex-column-fluid align-items-center justify-content-center w-100 bg-gray'>


    <Topabar data={props}/>

        <div className="container container-small top-18">
          <div className="card  card-shadow-none mb-xxl-4 mb-3">
            <div className="card-body p-3 needs-validation">

                <form
                  onSubmit={handleSubmit(onSubmit)}
                  noValidate
                  method="post"
                  action=""
                  autoComplete="off"
                  className="m-form m-form--fit m-form--label-align-right"
                  encType="multipart/form-data"
                >
               
               <div className="col-lg-12 col-sm-12">

               <input
                    {...register("tag_type", {
                      required: true,
                      required: "required field.",
                    })}
                    type="hidden"
                    defaultValue="2"
                  />
                <label className="fw-semibold text-muted mb-2">Tage Category 
                  <strong className="text-danger">*</strong></label> 
                      <input
                        {...register("tag_title", {
                          required: true,
                          required: "required field.",
                        })}
                        type="text"
                        className={`form-control form-control-lg mb-3 mb-lg-0 ${errors.tag_title ? `is-invalid` : null} `}
                        defaultValue=""
                      />
                      {errors.tag_title && (
                        <p className="text-error">{errors.tag_title.message}</p>
                      )}
                  </div>

                  <div className="d-flex align-items-center justify-content-end flex-wrap mt-3 top-18">
                    <button type="button" className="btn btn-sm btn-secondary me-3">Cancel</button>
                    <button disabled={isDisabled ? 'disabled' : null} 
                     type="submit" className="btn btn-sm btn-primary">Save { isDisabled ? <Spinner animation="border" role="status"> </Spinner> : null }</button>
                  </div>
                  </form>

                </div>
              </div>
          </div>
        </div>
     </>
   )
}

export default AddCategory
