import React, { useState } from "react";
import { Link } from 'react-router-dom';

function PaymentMethod(){

    const [checked, setChecked] = useState(true)

    return(
        <div className="container">
            <div className="row justify-content-md-center">
                <div className="col-lg-10 col-12 pt-lg-5 pt-3">
                    <div className="plan-details mb-md-5 mb-4">
                        <h6 className="mb-3">Payment Method</h6>
                        <div className="card">
                            <div className="card-body px-lg-4 py-lg-5 p-3">
                            <div className="paymentMethodWrap pt-0 mt-0">
                                <div className="payment-method-new">
                                    <div className="payment-box payment-method-box p-0">
                                        <div className="card-list-main w-100">
                                        <ul className="list-unstyled m-0">
                                            <li className="add-card-item position-relative">
                                                <input type="radio" className="cardSelect" name='cardSelect' defaultChecked={checked} onChange={()=> setChecked(!checked)} value="" />
                                                <div className="d-sm-flex border rounded bg-white add-card align-items-center px-3 py-2 default">
                                                    <span className="card-img me-3">
                                                        <img alt="Visa" src="https://test.taggbox.com/widget/assets/media/payment/visa.png" className="img-fluid"/>
                                                    </span>
                                                    <p className="card-number mb-0 text-dark">
                                                        <strong>Visa</strong><br/>
                                                        <span className="text-muted">**** ***** ****</span>
                                                    </p>
                                                    <p className="lable m-0 text-center">
                                                        <small className="label label-inline label-light-success chnage-card-text text-center">Default</small>
                                                    </p>
                                                    <span className="saprator border-start mx-2"></span>
                                                    <Link id="cardedit-button" className="btn btn-link edit-btn get-card-payment text-decoration-none"><small>EDIT</small></Link>
                                                    <Link className="btn btn-link delete-btn ms-2 text-decoration-none"><small>DELETE</small></Link>
                                                </div>
                                            </li>
                                            <li className="add-card-item position-relative">
                                                <input type="radio" className="cardSelect" name='cardSelect' value="" />
                                                <div className="d-sm-flex border rounded bg-white add-card align-items-center px-3 py-2">
                                                    <span className="card-img me-3">
                                                        <img alt="Visa" src="https://test.taggbox.com/widget/assets/media/payment/visa.png" className="img-fluid"/>
                                                    </span>
                                                    <p className="card-number mb-0 text-dark">
                                                        <strong>Visa</strong><br/>
                                                        <span>**** ***** ****</span>
                                                    </p>
                                                    <p className="lable m-0 text-center">
                                                        <small className="label label-inline chnage-card-text text-center">Make Default</small>
                                                    </p>
                                                    <span className="saprator border-start mx-2"></span>
                                                    <Link id="cardedit-button" className="btn btn-link edit-btn get-card-payment text-decoration-none">
                                                        <small>EDIT</small>
                                                    </Link>
                                                    <Link className="btn btn-link delete-btn ms-2 text-decoration-none">
                                                        <small>DELETE</small>
                                                    </Link>
                                                </div>
                                            </li>
                                        </ul>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            </div>
                        </div>
                    </div>

                    <div className="plan-details mb-md-5">
                        <div className="payment_info_header d-flex align-items-start justify-content-between">
                            <h6 className="mb-3">Add New Payment Method</h6>
                            <img className="img-fluid" src="https://test.taggbox.com/widget/assets/media/payment/powered-by-stripe-logo.png" alt="strip"/>
                        </div>
                        <div className="card">
                            <div className="card-body px-lg-4 py-lg-5 p-3">
                            <div className="paymentMethodWrap pt-0 mt-0">
                                <div className="payment-method-new">
                                    <div className="payment-box payment-method-box p-0">
                                        <div className="card-list-main w-100">
                                            <div className="d-sm-flex border rounded bg-white add-card align-items-center px-3 py-2">
                                                <span>Card No.</span>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default PaymentMethod;