import React from "react";
import { useState, useEffect } from "react";
import { Button, Table } from "react-bootstrap";
import Topabar from "../../Header/Components/Topbar";
import Modal from "react-bootstrap/Modal";
import { useForm } from "react-hook-form";
import { toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import { ADD_COMMISSION, GET_COMMISSION } from "../../../actions/authenticationAction";
import Spinner from 'react-bootstrap/Spinner';
import moment from 'moment'


function Add(props) {

  const [ commissionForm,setcommissionForm]=useState(false);  
  const [commissionListData, setcommissionListData] = useState(false)
 
  const [isDisabled, setDisabled] = useState(false);
  const [commissionList, setCommissionList] = useState([])

  const { register, handleSubmit, formState: { errors },reset,setValue } = useForm();

  const onSubmit = async (values) => {
    setDisabled(true);
    const { commission } = values
    ADD_COMMISSION(commission).then(res => {
    const { data } = res
      if(data.status){ 
           toast.success(data.msg)
           getCommission()
           setcommissionForm(false)
           setcommissionListData(true)
           reset();
        }
      else toast.error(data.msg)
      setDisabled(false);
    }).catch(error=>{
        setDisabled(false);
        toast.error(error.message)
    })
  }


   const getCommission=()=>{

    GET_COMMISSION().then(res => {
        console.log("values-", res)
        const { data } = res
        if(data.status){
          setCommissionList(data.data)
        }
      })
   }

  useEffect(() => {
    getCommission()
  }, [])


  return (
    <>
      <div className="d-flex flex-column flex-column-fluid align-items-center justify-content-center w-100 bg-gray">
      
        <Topabar data={props} />
        
        <div className="container">
          <div className="row justify-content-md-center">
            <div className="col-lg-10 col-12 pt-lg-5 pt-3">
              <div className="plan-details mb-md-5 mb-4">
                <div className="card">
                  <div className="card-body px-lg-4 py-lg-5 p-3">
                    <div className="row align-items-md-center">

                    { commissionList && commissionList.length ?

                      <>  
                       <div className="col-lg-3 col-sm-6 col-12">
                        <div className="mb-md-0 mb-4">
                          <p className="mb-md-4 mb-2">Commission</p>
                          <h6>{commissionList[0].commission_amount}</h6>
                        </div>
                      </div>

                      <div className="col-lg-3 col-sm-6 col-12">
                        <div className="mb-md-0 mb-4">
                          <p className="mb-md-4 mb-2">Last Update</p>
                          <h6>{moment(commissionList[0].created_at).format('DD-MM-YYYY')}</h6>
                        </div>
                      </div>
                      </>
                      : null }


                      <div className="col-lg-3 col-sm-6 col-12">
                        <div className="text-md-center">
                          <button
                            onClick={(e) => setcommissionForm(true)}
                            className="btn btn-primary d-flex align-items-center"
                          >
                            <small>Upgrade Now</small>
                          </button>
                        </div>
                      </div>

                      <div className="col-lg-3 col-sm-6 col-12">
                        <div className="text-md-center">
                          <button
                            onClick={(e) => setcommissionListData(true)}
                            className="btn btn-primary d-flex align-items-center"
                          >
                            <small>Commissions</small>
                          </button>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>

        <Modal show={commissionForm} onHide={(e) => setcommissionForm(false)}>
          <Modal.Header closeButton>
            <Modal.Title>Upgrade Commission</Modal.Title>
          </Modal.Header>

          <form
            onSubmit={handleSubmit(onSubmit)}
            noValidate
            method="post"
            action=""
            autoComplete="off"
            className="m-form m-form--fit m-form--label-align-right"
            encType="multipart/form-data"
          >
            <Modal.Body>
              <div className="row mb-12 mb-xl-12">
                <div className="col-lg-12 col-sm-12">
                  <label className="fw-semibold text-muted mb-2">
                    Amount <strong className="text-danger">*</strong>
                  </label>
                  <input
                    {...register("commission", {
                      required: true,
                      required: "required field.",
                    })}
                    type="number"
                    className={`form-control form-control-lg mb-3 mb-lg-0 ${errors.commission ? `is-invalid` : null} `}
                    defaultValue=""
                  />
                  {errors.commission && (
                    <p className="text-error">{errors.commission.message}</p>
                  )}
                </div>
              </div>
            </Modal.Body> { console.log(isDisabled,'isDisabledisDisabled')}
            <Modal.Footer>
              <Button ty
                variant="secondary"
                onClick={(e) => setcommissionForm(false)}
              >
                Close
              </Button>
              <Button
                type="submit"
                disabled={isDisabled ? 'disabled' : null}
                variant="primary">
                Save  { isDisabled ? <Spinner animation="border" role="status"> </Spinner> : null }
              </Button>

             
            </Modal.Footer>
          </form>
        </Modal>

        <Modal show={commissionListData} onHide={(e) => setcommissionListData(false)}>
          <Modal.Header closeButton>
            <Modal.Title> Commissions</Modal.Title>
          </Modal.Header>
          <Modal.Body>

          { commissionList && commissionList.length ?  

            <Table responsive className="mb-0">
              <thead>
                <tr>
                  <th className="p-lg-6 p-sm-3 p-2">#</th>
                  <th className="p-lg-6 p-sm-3 p-2">Amount</th>
                  <th className="p-lg-6 p-sm-3 p-2">Date</th>
                </tr>
              </thead>
              <tbody>

              { commissionList.map((dt,index) => (
                <tr className="align-middle" style={{ color: index ? `red` : `green` }}>
                  <td className="p-lg-6 p-sm-3 p-2 text-nowrap">{index+1}</td>
                  <td className="p-lg-6 p-sm-3 p-2 text-nowrap">{dt.commission_amount}</td>
                  <td className="p-lg-6 p-sm-3 p-2 text-nowrap">{moment(dt.created_at).format('DD-MM-YYYY')}</td>
                </tr>
             ))}     
          
              </tbody>
            </Table> : <div className="data-not-found">Data not exist</div> }
          </Modal.Body>
        </Modal>
      </div>
    </>
  );
}

export default Add;
