import React from 'react';
import { useState, useEffect } from 'react';
import { Link,useNavigate, } from 'react-router-dom';
import { LOGIN } from '../../actions/authenticationAction';
import { toast } from 'react-toastify';
import { useForm } from "react-hook-form";
import { Spinner } from 'react-bootstrap';
import 'react-toastify/dist/ReactToastify.css';
import { ToastContainer } from 'react-toastify';
import { BASE_URL } from '../../actions/api';

function Login(props) {

  const { register, handleSubmit, formState: { errors },reset,setValue } = useForm();
  const [isDisabled, setDisabled] = useState(false);
  let navigate = useNavigate()

  const onSubmit = async (values) => { 
    setDisabled(true)
    LOGIN(values).then(res => {
      const { data } = res
      if (data.status) {
        localStorage.setItem("token", "true")
        toast.success(data.msg)
        window.location.href=(BASE_URL);
      }else toast.error(data.msg)
      setDisabled(false)
    }).catch(error=>{
      setDisabled(false);
      toast.error(error.message)
    })
  }


  useEffect(() => {

  }, [])

  return (
    <>
    

      <div className='d-flex flex-column flex-column-fluid align-items-center justify-content-center w-100 bg-gray lex-column-login'>
       
         <ToastContainer 
            position="bottom-right"
            autoClose={1000}
            hideProgressBar={false}
            newestOnTop={false}
            closeOnClick
            rtl={false}
            pauseOnFocusLoss
            draggable
            pauseOnHover
            theme="colored" />
        <div className="container container-small loginPage pading-0">
          <div className="flex-row-fluid flex-column-login">
            <div className="app-container pading-0">
              <div className="d-flex flex-root flex-column-login">
                <div className="d-flex flex-lg-row flex-column-fluid flex-column-login">

                  <div className="d-flex flex-column flex-column-fluid flex-center w-lg-50 p-10 lex-column-login">
                    <div className="d-flex justify-content-between flex-column-fluid flex-column w-100 mw-450px lex-column-login">
                      <div className="py-20">
                        <form onSubmit={handleSubmit(onSubmit)} className="form w-100 fv-plugins-bootstrap5 fv-plugins-framework" noValidate="novalidate" data-kt-redirect-url="" action="#">
                          <div className="card-body">
                            <div className="text-start mb-10">
                              <h1 className="text-dark mb-3 fs-3x login-heading" data-kt-translate="sign-in-title"> Sign In</h1>
                              <div className="text-gray-400  fs-6" data-kt-translate="general-desc">Fallovers access to continue</div>
                            </div>

                            <div className="fv-row mb-8 fv-plugins-icon-container fv-plugins-bootstrap5-row-invalid">
                              <input type="text"  {...register("email", {
                                    required: true,
                                    required: "required field.",
                                  })}   placeholder="Email" autoComplete="off" data-kt-translate="sign-in-input-email" className={`form-control form-control-solid ${errors.email ? `is-invalid` : null}`} />
                               
                             
                            <div className="fv-plugins-message-container invalid-feedback"><div data-field="email" data-validator="notEmpty">Email address is required</div></div></div>
                            <div className="fv-row mb-7 fv-plugins-icon-container fv-plugins-bootstrap5-row-invalid">
                              <input type="text"  {...register("password", {
                                  required: true,
                                  required: "required field.",
                                })} placeholder="Password" name="password" autoComplete="off" data-kt-translate="sign-in-input-password" className={`form-control form-control-solid ${errors.password ? `is-invalid` : null}`} />
                               

                              <div className="fv-plugins-message-container invalid-feedback"><div data-field="password" data-validator="notEmpty">The password is required</div></div></div>
                            <div className="d-flex flex-stack flex-wrap gap-3 fs-base fw-semibold mb-10">
                              <div />
                              <Link to="../forgot-password" className="link-primary" data-kt-translate="sign-in-forgot-password">Forgot Password ?</Link>
                            </div>

                            <div className="d-flex flex-stack">
                              <button type='submit' disabled={isDisabled ? 'disabled' : null}  id="kt_sign_in_submit" className="btn btn-primary me-2 flex-shrink-0">
                                <span className="indicator-label" data-kt-translate="sign-in-submit">Sign In { isDisabled ? <Spinner animation="border" role="status"> </Spinner> : null }</span>
                                {/* <span className="indicator-progress">
                                  <span data-kt-translate="general-progress">Please wait...</span>
                                  <span className="spinner-border spinner-border-sm align-middle ms-2" />
                                </span> */}
                              </button>
                            </div>
                          </div>
                        </form>
                      </div>
                    </div>
                  </div>

                  {/* <div className="d-none d-lg-flex flex-lg-row-fluid w-50 bgi-size-cover bgi-position-y-center bgi-position-x-start bgi-no-repeat loginRight" />
            <div className='loginRight'></div> */}
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  )
}

export default Login
