export const ADD_CATEGORY = (value) => {
    const formData = new FormData()
    formData.append("tag_title", value.tag_title)
    formData.append("tag_type", value.tag_type)

     if(value.tag_type==2){
        formData.append("tag_parent_id", value.tag_parent_id)
      }

    return axios.post(addCategory, formData)
}


export const EDIT_CATEGORY = (value) => {
    const formData = new FormData()
    formData.append("tag_title", value.tag_title)
    formData.append("tag_type", value.tag_type)
    formData.append("id", value.tag_id)

     if(value.tag_type==2){
        formData.append("tag_parent_id", value.tag_parent_id)
      }

    return axios.post(editCategory, formData)
}


export const STATUS_UPDATE_CATEGORY = (id,type) => {
    const formData = new FormData()
    formData.append("id", encode(id))
    formData.append("data", type)
    return axios.post(statusCategory, formData)
}


export const ADD_COMMISSION = (value) => {
    const formData = new FormData()
    formData.append("commission_amount", value)
    return axios.post(addCommission, formData)
}



export const UPDATE_MAIL_STATUS = (id,type) => {
    const formData = new FormData()
    formData.append("id", encode(id))
    formData.append("data", type)
    return axios.post(updateMailStatus, formData)
}



