export const ADD_MANPOWER = (value) => {
    const formData = new FormData()
    formData.append("admin_roll", value.admin_roll)
    formData.append("admin_name", value.admin_name)
    formData.append("admin_mobile_no", value.admin_mobile_no)
    formData.append("admin_alternate_mobile_no", value.admin_alternate_mobile_no)
    formData.append("admin_mail_id", value.admin_mail_id)
    formData.append("admin_address", value.admin_address)

    return axios.post(addManpower, formData)
}


export const GET_MANPOWER = () => {
    return axios.post(getManpower, {})
}
