import React from 'react';
import { useState,useEffect } from 'react';
import { useForm } from "react-hook-form";
import { toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import { Spinner } from 'react-bootstrap';
import { ADD_MANPOWER } from '../../../actions/authenticationAction';
import Topabar from '../../Header/Components/Topbar';


  function Add(props) {

    const [isDisabled, setDisabled] = useState(false);
    const { register, handleSubmit, formState: { errors },reset,setValue } = useForm();
  
    const onSubmit = async (values) => {
      setDisabled(true);
      ADD_MANPOWER(values).then(res => {
      const { data } = res
        if(data.status){ 
             toast.success(data.msg)
             reset()
          }
        else toast.error(data.msg)
        setDisabled(false);
      }).catch(error=>{
          setDisabled(false);
          toast.error(error.message)
      })
    }

     useEffect(()  => { 
     
     }, []) 

    return (
      <>

<div className='d-flex flex-column flex-column-fluid align-items-center justify-content-center w-100 bg-gray'>
    <Topabar data={props}/>
      
        <div className="container container-small top-18">
          <div className="card card-shadow-none mb-xxl-4 mb-3">
            <div className="card-body p-3 needs-validation">

            <form
                  onSubmit={handleSubmit(onSubmit)}
                  noValidate
                  method="post"
                  action=""
                  autoComplete="off"
                  className="m-form m-form--fit m-form--label-align-right"
                  encType="multipart/form-data"
                >
 
            <div className='row'>
              <div className="col-lg-6 col-sm-12  input-block">
                <label className="fw-semibold text-muted mb-2">Manager Roll
                  <strong className="text-danger">*</strong></label> {/** is-valid */}
                    <select  {...register("admin_roll", {
                          required: true,
                          required: "required field.",
                        })}   className={`form-control form-control-lg mb-3 mb-lg-0  ${errors.admin_name ? `is-invalid` : null}`}>
                       <option>--Select-- </option>
                       <option value={'BM'}>Business development manager  </option>
                       <option value={'AM'}>Accounting manager </option>
                       <option value={'DM'}>Dispute resolution manager</option>
                    </select>

                      {errors.admin_name && (
                        <p className="text-error">{errors.admin_name.message}</p>
                      )}
                  </div>

              <div className="col-lg-6 col-sm-12 input-block">
                <label className="fw-semibold text-muted mb-2"> Name
                  <strong className="text-danger">*</strong></label>  
                    <input {...register("admin_name", {
                          required: true,
                          required: "required field.",
                        })}  type="text" className={`form-control form-control-lg mb-3 mb-lg-0 ${errors.admin_name ? `is-invalid` : null}`} defaultValue=""/>
                  
                        {errors.admin_name && (
                          <p className="text-error">{errors.admin_name.message}</p>
                        )}
                  </div>


               <div className="col-lg-6 col-sm-12 input-block">
                <label className="fw-semibold text-muted mb-2"> Mobile No
                  <strong className="text-danger">*</strong></label> 
                    <input  {...register("admin_mobile_no", {
                          required: true,
                          required: "required field.",
                        })}  type="number" className={`form-control form-control-lg mb-3 mb-lg-0 ${errors.admin_mobile_no ? `is-invalid` : null}`} defaultValue=""/>
                        {errors.admin_mobile_no && (
                          <p className="text-error">{errors.admin_mobile_no.message}</p>
                        )}
                  </div>


              <div className="col-lg-6 col-sm-12 input-block">
                <label className="fw-semibold text-muted mb-2"> Alternate Mobile No</label> 
                    <input  {...register("admin_alternate_mobile_no")} type="number" className={`form-control form-control-lg mb-3 mb-lg-0 ${errors.admin_alternate_mobile_no ? `is-invalid` : null}`} defaultValue=""/>
                  </div>


               <div className="col-lg-6 col-sm-12 input-block">
                <label className="fw-semibold text-muted mb-2"> Email Id
                  <strong className="text-danger">*</strong></label>
                    <input  {...register("admin_mail_id", {
                          required: true,
                          required: "required field.",
                        })} type="email"  className={`form-control form-control-lg mb-3 mb-lg-0 ${errors.admin_mail_id ? `is-invalid` : null}`} defaultValue=""/>
                     
                       {errors.admin_mail_id && (
                          <p className="text-error">{errors.admin_mail_id.message}</p>
                        )}
                  
                  </div>
 


              <div className="col-lg-6 col-sm-12 input-block">
                <label className="fw-semibold text-muted mb-2"> Address
                  <strong className="text-danger">*</strong></label> 
                    <input  {...register("admin_address", {
                          required: true,
                          required: "required field.",
                        })} type="text" className={`form-control form-control-lg mb-3 mb-lg-0 ${errors.admin_address ? `is-invalid` : null}`} defaultValue=""/>
                  
                         {errors.admin_address && (
                          <p className="text-error">{errors.admin_address.message}</p>
                        )}
                  </div>

               </div> 

               <div className="row mb-3 mb-xl-4 top-18">
                <label className="col-sm-12 fw-semibold text-muted mb-2">Profile Pic</label>
                <div className="col-sm-12">
                  <div className="d-flex align-items-center">
                    <div className="me-3 me-xxl-4">
                      <div className="symbol h-90px w-90px position-relative rounded-circle overflow-hidden">
                        <img src="https://i.pinimg.com/736x/bc/4b/bd/bc4bbd2ce6b43c390187869173b36130.jpg" alt="profile img" className="img-fluid object-center-fit" />
                      </div>
                    </div>
                    <div className="d-flex align-items-center flex-wrap">
                      <div className="d-flex flex-column align-items-center flex-nowrap mb-0">
                        <button type="button" className="btn btn-icon btn-active-light p-0 w-30px h-30px transition-none border-0 my-1">
                          <span className="svg-icon svg-icon-5">
                            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" width={24} height={24}>
                              <path fill="none" d="M0 0h24v24H0z" />
                              <path d="M12.9 6.858l4.242 4.243L7.242 21H3v-4.243l9.9-9.9zm1.414-1.414l2.121-2.122a1 1 0 0 1 1.414 0l2.829 2.829a1 1 0 0 1 0 1.414l-2.122 2.121-4.242-4.242z" fill="currentColor" />
                            </svg>
                          </span>
                        </button>
                        <button type="button" className="btn btn-icon btn-active-light p-0 w-30px h-30px transition-none border-0 my-1">
                          <span className="svg-icon svg-icon-5">
                            <svg xmlns="http://www.w3.org/2000/svg" className="symbol-20" viewBox="0 0 24 24" width={24} height={24}>
                              <path fill="none" d="M0 0H24V24H0z" />
                              <g><path d="M17 6h5v2h-2v13a1 1 0 0 1-1 1H5a1 1 0 0 1-1-1V8H2V6h5V3a1 1 0 0 1 1-1h8a1 1 0 0 1 1 1v3zm1 2H6v12h12V8zm-9 3h2v6H9v-6zm4 0h2v6h-2v-6zM9 4v2h6V4H9z" fill="currentColor" /></g>
                            </svg>
                          </span>
                        </button>
                      </div>
                    </div>
                  </div>
                  <div className="form-text mt-2">Allowed file types: png, jpg, jpeg.</div>
                </div>
              </div>

                <div className="d-flex align-items-center justify-content-end flex-wrap mt-3 top-18">
                  <button type="button" className="btn btn-sm btn-secondary me-3">Cancel</button>
                  <button disabled={isDisabled ? 'disabled' : null} 
                     type="submit" className="btn btn-sm btn-primary">Save { isDisabled ? <Spinner animation="border" role="status"> </Spinner> : null }</button>
                </div>      

              </form>   
           
            </div>
          </div>
        </div>
      </div>
     </>
   )
}

export default Add
