import { encode } from 'js-base64';
import React from 'react';
import { useState,useEffect } from 'react';
import { OverlayTrigger, Table, Tooltip } from 'react-bootstrap';
import { confirmAlert } from 'react-confirm-alert';
import { Link } from 'react-router-dom';
import { toast } from 'react-toastify';
import { GET_SUB_CATEGORY, STATUS_UPDATE_CATEGORY } from '../../../../actions/authenticationAction';
import Topabar from '../../../Header/Components/Topbar';
import Pagging from '../../include/Pagging';

  function SubCategory(props) {

    const [getData, setgetData] = useState([]);

    const changeStatus=(id,type)=>{

      STATUS_UPDATE_CATEGORY(id,type).then(res => {
        const { data } = res
          if(data.status){ 

                if(type=='del'){
                  setgetData(getData.filter((data)=>{ 
                        return data.tag_category_id!=id 
                    }))
                }

               toast.success(data.msg)
            }
          else toast.error(data.msg)
        }).catch(error=>{
            toast.error(error.message)
        })
    }


    
  const deleteData = (id,type)=>{
     confirmAlert({
      message: 'Are you sure delete this record !',
      buttons: [
        {
          label: 'Yes',
          onClick: () => changeStatus(id,type)
        },
        {
          label: 'No',
          onClick: () => {}
        }
      ]
   });
  }  

      useEffect(()  => { 

        GET_SUB_CATEGORY().then(res => {
            const { data } = res
            if(data.status){
               setgetData(data.data)
            }
        })
     
 }, []) 

    return (
      <>

<div className='d-flex flex-column flex-column-fluid align-items-center justify-content-center w-100 bg-gray'>
    <Topabar data={props}/>

                    <div className="container container-small top-18">
                        <div className="card card-shadow-none bottom-15">
                            <div className="card-body p-lg-4 p-3">
                                <Table responsive className="mb-0">
                                    <thead>
                                        <tr>
                                            <th className="p-lg-4 p-sm-3 p-2">#</th>
                                            <th className="p-lg-4 p-sm-3 p-2">Category</th>
                                            <th className="p-lg-4 p-sm-3 p-2">Sub Category</th>
                                            <th className="text-end p-lg-4 p-sm-3 p-2" align="right">Action</th>
                                        </tr>
                                    </thead>
                                    <tbody>

                                    { getData.length>0   ?  

                                      getData.map((dt,index) => (

                                        <tr className="align-middle">
                                            <td className="p-lg-4 p-sm-3 p-2 text-nowrap">{index+1}</td>
                                            <td className="p-lg-4 p-sm-3 p-2 text-nowrap">{dt.tag_title}</td>
                                            <th className="p-lg-4 p-sm-3 p-2">{dt.subCateory}</th>

                                            <td className="p-lg-4 p-sm-3 p-2 text-nowrap text-end">
                                               <OverlayTrigger placement="top" overlay={<Tooltip>Active</Tooltip>}>
                                                 <button className="btn btn-icon btn-active-light">
                                                    <label className='switch position-relative'>
                                                        <input  onChange={(e)=>changeStatus(dt.tag_category_id,'type')} defaultChecked={dt.tag_status ? true : false} type="checkbox" role="switch" id="flexSwitchCheckDefault"/>
                                                        <span></span>
                                                    </label>
                                                </button></OverlayTrigger> &nbsp;&nbsp;

                                            <OverlayTrigger placement="top" overlay={<Tooltip>Edit</Tooltip>}>
                                              <Link to={`/edit-sub-ategory/${encode(dt.tag_category_id)}`}>
                                                  <button className="btn btn-icon btn-active-light">
                                                    <svg width="24" height="24" fill="none" xmlns="http://www.w3.org/2000/svg"><g clip-path="url(#a)" fill-rule="evenodd" 
                                                        clip-rule="evenodd" fill="#212529"><path d="M21.707 7.293a1 1 0 0 1 0 1.414l-13 13A1 1 0 0 1 8 22H4a1 1 0 0 1-1-1v-4a1 1 0 0 1 .293-.707l13-13a1 1 0 1 1 1.414 1.414L5 17.414V20h2.586L20.293 7.293a1 1 0 0 1 1.414 0z"/><path d="M16.293 3.293a1 1 0 0 1 1.414 0l4 4a1 1 0 0 1-1.414 1.414l-4-4a1 1 0 0 1 0-1.414zM17.293 11.707l-4-4 1.414-1.414 4 4-1.414 1.414z"/></g><defs><clipPath id="a"><path fill="#fff" d="M0 0h24v24H0z"/></clipPath></defs></svg>
                                                  </button>
                                                </Link>
                                              </OverlayTrigger>

                                            <OverlayTrigger placement="top" overlay={<Tooltip>Delete</Tooltip>}>
                                                <button onClick={(e)=>deleteData(dt.tag_category_id,'del')}  className='btn btn-icon btn-active-light'>
                                                    <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24">
                                                        <g data-name="Group 4331" transform="translate(10956 -4533)">
                                                            <rect id="Rectangle_2069" data-name="Rectangle 2069" width="24" height="24" transform="translate(-10956 4533)" fill="none" opacity="0"></rect>
                                                            <g id="Group_4324" data-name="Group 4324" transform="translate(-10952 4536)">
                                                            <path id="Path_5188" data-name="Path 5188" d="M2.722,16.328A2,2,0,0,0,4.694,18h6.612a2,2,0,0,0,1.972-1.672L14,6H2ZM9,9.5A.5.5,0,0,1,9.5,9h1a.5.5,0,0,1,.5.5v5a.5.5,0,0,1-.5.5h-1a.5.5,0,0,1-.5-.5Zm-4,0A.5.5,0,0,1,5.5,9h1a.5.5,0,0,1,.5.5v5a.5.5,0,0,1-.5.5h-1a.5.5,0,0,1-.5-.5ZM15,2H11A2,2,0,0,0,9,0H7A2,2,0,0,0,5,2H1A1,1,0,0,0,1,4H15a1,1,0,0,0,0-2Z" fill="#545454" fill-rule="evenodd"></path>
                                                            </g>
                                                        </g>
                                                        </svg>
                                                    </button></OverlayTrigger>
                                                    
                                                </td>
                                        </tr>
                                      )) : 
                                      <tr>
                                        <td colSpan={4}>
                                        <div className="data-not-found">Data not exist</div>
                                      </td></tr> }
                                    </tbody>
                                </Table>
                            </div>
                        </div>

                    { getData.length >0 ? <Pagging/> : null }
  
                </div>
          </div>                                                               
     </>
   )
}

export default SubCategory
