import {Nav, Tab } from 'react-bootstrap';
import React, { Component, Suspense } from 'react';
import './billing.scss';
import { Link } from 'react-router-dom';
import Overview from './Components/Overview';
import PaymentMethod from './Components/paymentMethod';
import Invoice from './Components/Invoice';

class Billing extends Component {
    render() {
        return (
            <div className='d-flex flex-column flex-column-fluid w-100 flex-position-center pt-2 content'>
                <div className="position-fixed bg-white border-bottom subheader">
                    <div className="container-fluid d-flex align-items-center justify-content-between flex-wrap h-100">
                        <div className="d-flex align-items-center flex-wrap">
                            <Link to="/home" className='d-flex align-items-center me-2 text-decoration-none text-muted'>HOME</Link>
                            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 320 512" height="15" width="15" className="menu-arrow fill-primary me-2"><path d="M278.6 233.4c12.5 12.5 12.5 32.8 0 45.3l-160 160c-12.5 12.5-32.8 12.5-45.3 0s-12.5-32.8 0-45.3L210.7 256 73.4 118.6c-12.5-12.5-12.5-32.8 0-45.3s32.8-12.5 45.3 0l160 160z" fill="#2B2B2B"/></svg>
                            <span className="font-weight-bold mr-4">BILLING</span>
                        </div>
                    </div>
                </div>
                <div className="billing-box">
                <Tab.Container id="billing-info-tab" defaultActiveKey="overview">
                    <Nav className="flex-column border-bottom custom-tabs">
                        <Nav.Item className="d-flex">
                            <Nav.Link eventKey="overview" className="text-primary">Overview</Nav.Link>
                            <Nav.Link eventKey="paymentMethod" className="text-primary">Payment Method</Nav.Link>
                            <Nav.Link eventKey="invoice" className="text-primary">Invoice</Nav.Link>
                        </Nav.Item>
                    </Nav>
                    <Tab.Content>
                        <Tab.Pane eventKey="overview">
                            <Overview/>
                        </Tab.Pane>
                        <Tab.Pane eventKey="paymentMethod">
                            <PaymentMethod/>
                        </Tab.Pane>
                        <Tab.Pane eventKey="invoice">
                            <Invoice/>
                        </Tab.Pane>
                    </Tab.Content>
                </Tab.Container>
                </div>
                
            </div>
        );
    }
}

export default Billing;