import React, { Component, Suspense } from 'react';
import { Link } from 'react-router-dom';
const AssetsModal = React.lazy(() => import('../MyAssets/AssetsModal/AssetsModal'));
class Recents extends Component {

    render() {
            return <div className='d-flex flex-column flex-column-fluid align-items-center justify-content-center w-100'>
                    <div className="position-fixed bg-white border-bottom subheader">
                        <div className="container-fluid d-flex align-items-center justify-content-between flex-wrap h-100">
                            <div className="d-flex align-items-center flex-wrap">
                                <h1 className="text-muted font-weight-bold mr-4 fs-6 mb-0">Recents</h1>
                            </div>
                            <div className="d-flex align-items-center flex-wrap">
                                
                            </div>
                        </div>
                    </div>
                    <div className="container-fluid">
                        <ul className="breadcrumb breadcrumb-separatorless fw-semibold fs-7 mb-2 pt-2">
                            <li className="breadcrumb-item text-muted">
                                <Link to={"/"} className="text-muted text-hover-primary text-decoration-none">Home</Link>
                            </li>
                            <li className="breadcrumb-item text-muted">Recents</li>
                        </ul>
                    </div>
                    <div className="container-fluid">
                        <div className="mt-xxl-4 mt-2">
                            <h3 className="fs-6">Recents</h3>
                        </div>
                    </div>
                    <div className="container-fluid">
                        
                    </div>
                    <Suspense fallback={<div>Loading...</div>}>
                        <AssetsModal/>
                    </Suspense>
                </div>
              
    }
}

export default Recents;